@media (min-width: 900px) {
  .innercontainer {
    max-width: 956px;
  }
  .innercontainer--werken-bij {
    max-width: 756px;
  }
  .innercontainer--ctp {
    max-width: 650px;
  }
}

@media (min-width: 1100px) {
  .footer-desktop .footer-menu-flex {
    gap: 2em;
  }
}

@media (min-width: 1400px) {
  .innercontainer {
    max-width: 1156px;
  }
  .innercontainer--werken-bij {
    max-width: 880px;
  }
  .innercontainer--ctp {
    max-width: 756px;
  }
  .landing-flex {
    display: flex;
    align-items: center;
  }
  .contact {
    img {
      max-height: 350px;
      width: auto;
      margin-left: 0;
    }
  }
}
@media (min-width: 1700px) {
  .fixed-menu {
    width: 90%;
  }
}

@media (min-width: 1870px) {
  .fixed-menu {
    width: 80%;
  }
}

@media (min-width: 2000px) {
  .fixed-menu {
    width: 70%;
  }
}
